@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
body {
  margin: 0;
}

* {
  font-family: "Roboto", sans-serif;
}

.landing {
  position: relative;
  background-size: cover;
  background-position: center;
  height: 100vh;
  width: 100vw;
  margin-top: -24px;
  margin-bottom: -50px;
}

.top-div {
  width: 100%;
  min-height: 50vh;
  background: transparent linear-gradient(248deg, #303f60 0%, #1a253c 100%) 0%
    0% no-repeat padding-box;
  margin: 0;
  margin-bottom: 90px;
}

.top-div .left {
  color: #ffffff;
  font-size: 60px;
}

.top-div .left span {
  color: #43afff;
}

.getStarted {
  font-size: 16px;
  background-color: #43afff;
  color: #fff;
  padding: 13px 30px;
  border: 1px solid #43afff;
  border-radius: 5px;
  margin-bottom: 50px;
}

.img1 {
  width: 50vw;
  position: absolute;
  top: 70px;
  right: 10%;
}

.companies img {
  width: 90%;
  height: 55px;
  vertical-align: bottom !important;
}

.form-control::-webkit-input-placeholder {
  color: #bbb !important;
}

.form-control:-ms-input-placeholder {
  color: #bbb !important;
}

.form-control::-ms-input-placeholder {
  color: #bbb !important;
}

.form-control::placeholder {
  color: #bbb !important;
}

.navbar {
  background: transparent linear-gradient(248deg, #303f60 0%, #1a253c 100%) 0%
    0% no-repeat padding-box;
}

.login {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 65%;
  z-index: -1;
  padding-top: 100px;
  background: transparent linear-gradient(248deg, #303f60 0%, #1a253c 100%) 0%
    0% no-repeat padding-box;
  width: 100vw;
}

div.col-md-8.m-auto {
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 30px 36px #557da526;
  border-radius: 20px;
  padding: 30px;
}

.forgot,
.signup span {
  color: #43afff;
}

input.btn.btn-info.btn-block.mt-4 {
  width: 40%;
  margin: 20px auto 10px;
  background-color: #43afff;
}

.modal-content {
    width: 760px !important;
    right: 20%;
}

/* common classes */
.flex {
  display: flex;
}

.flex-row {
  flex-direction: row;
}

.flex-column {
  flex-direction: column;
}

.flex-jsb {
  justify-content: space-between;
}

.flex-center {
  justify-content: center;
}

.mrgn-10 {
  margin: 10px !important;
}

.mrgn-t-20 {
  margin-top: 20px;
}

.mrgn-t-80 {
  margin-top: 80px;
}

.mrgn-b-20 {
  margin-bottom: 20px;
}

.mrgn-b-40 {
  margin-bottom: 40px;
}

.mrgn-r-15 {
  margin-right: 15px;
}

.mrgn-l-15 {
  margin-left: 15px;
}

.font-22 {
  font-size: 22px;
}

.font-white {
  color: white !important;
}

.font-blue {
  color: #43afff;
}

.align-center {
  text-align: center;
}

.card {
  width: 341px;
height: 192px;
box-shadow: 0px 3px 6px #557DA526;
padding: 20px;
}

.title {
  color: #43AFFF;
  text-transform: capitalize;
  height: 53px;
  max-width: 50%;
  font-size: 22px;
  line-height: 27px;
}

.des {
  color: #1A253C;
  line-height: 20px;
  font-size: 14px;
  margin-top: 29px;
}

ul.ant-pagination {
    list-style-type: none !important;
}

ul.ant-pagination li {
  display: inline-block;
  border: 1px solid #d0d0ce;
  padding: 5px 10px;
  margin: 0 2px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 4px;
}

li.ant-pagination-item-active {
  background: #D9EFFF;
  border: none;
  cursor: default;
}

li.ant-pagination-disabled {
  opacity: 0.4;
  background: #d0d0ce;
  pointer-events: none;
}

button.ant-pagination-item-link {
  border: none;
  background: none;
}

li.ant-pagination-prev, li.ant-pagination-next {
  width: 30px;
  display: inline-flex;
  padding: unset !important;
  height: 36px;
}

.jf {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 70%;
  z-index: -1;
  padding-top: 100px;
  background: transparent linear-gradient(248deg, #303F60 0%, #1A253C 100%) 0% 0% no-repeat padding-box;
  width: 100vw;
}

.appCard {
  width: 260px;
  background: #FFFFFF 0% 0% no-repeat;
  height: 159px;
  border: 1px solid #303F6080;
  border-radius: 5px;
  color: #303F60;
  padding: 14px;
  
}

.avatar {
  background-color: #D9EFFF;
  padding: 12px 14px;
  border-radius: 50%;
}

.name, .email, .skillsList {
  font-size: 12px;
  overflow-x: hidden;
}

.name {
  margin-top: 10px;
  margin-bottom: 7px;
}

.skillsList span, .name {
  font-weight: 500;
}

.skillsList {
  margin-top: 12px;
}

.cut-text { 
  text-overflow: ellipsis;
  overflow: hidden; 
  width: 160px; 
  height: 1.2em; 
  white-space: nowrap;
}

